.about{
    // height: 100vh;
    padding-top: 7%;
    padding-bottom: 7%;
    background-color: #F6F5F4;
    &_advantages{
        display: flex;
        flex-direction: column;
        font-family: 'Montserrat';
        color: #1F373D;
        h2{
            font-weight: 600;
            font-size: 43px;
            margin-bottom: 3%;
        }
        p{
            font-size: 15px;
            line-height: 30px;   
        }
        .preview{
            display: flex;
            margin-bottom: 5%;
            .forma{
                display: flex;
                flex-direction: column;
                justify-content: space-around;
                form{
                    display: flex;
                    flex-direction: column;
                    
                    input{
                        margin-bottom: 5%;
                        border-radius: 10px;
                        outline: none;
                        border: solid 1px #FFCC01 ;
                        color: #1F373D;
                    }
                    textarea{
                        outline: none;
                        resize: none;
                        border: solid 1px #FFCC01 ;
                        border-radius: 10px;
                    }
                }
            }
            &-img{
            
                
                &_number{
                    padding: 3% 10%;
                    background-color: #FFCC01;
                    text-decoration: none;  
                    color: #1F373D;
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    font-weight: bold;
                    i{
                        color: #fff;
                    }
                }
            }
            
        }
        &__card{
            display: flex;
            flex-direction: column;
            align-items: center;
            &-text{
                display: flex;
            }
        }
    }      
}
